.container {
  margin: 25px;
  width: 460px;
  height: calc(100% - 50px);
  background-color: #454E54;
  position: fixed;
  display: flex;
  flex-direction: column;
  border-radius: 5px; }

.navHeading {
  color: #85898b;
  font-size: 1.3rem;
  margin: 2rem 0 1rem 1rem; }

.sidebarHeading {
  padding-bottom: 1.5rem;
  border-bottom: 1px solid #535657;
  width: 100%;
  display: flex;
  justify-content: center;
  font-size: 2rem;
  color: #f2f1ef;
  margin: 2rem 0; }

.sidebar {
  padding: 0 0.5rem;
  width: 100%;
  padding-bottom: 1.5rem;
  border-bottom: 1px solid #535657; }
  .sidebarEl {
    padding: 0.2rem 0;
    font-size: 1.5rem;
    color: #85898b;
    margin: 0.2rem 0;
    display: flex;
    width: 100%;
    align-items: center; }
    .sidebarEl:hover {
      background-color: #1E2024;
      cursor: pointer;
      color: #f2f1ef;
      border-radius: 6px; }
  .sidebar_icon {
    font-size: 2rem;
    padding: 1rem;
    margin: 0 1rem 0 1rem;
    color: #85898b; }
